import BigNumber from 'bignumber.js'
import React, { useEffect, useState } from 'react'
import CountUp from 'react-countup'
import styled from 'styled-components'
import { useWallet } from 'use-wallet'
import CardHome from '../../../components/CardHome'
import CardContent from '../../../components/CardContent'
import Label from '../../../components/Label'
import Spacer from '../../../components/Spacer'
import Value from '../../../components/Value'
import SushiIcon from '../../../components/SushiIcon'
import useAllEarnings from '../../../hooks/useAllEarnings'
import useAllStakedValue from '../../../hooks/useAllStakedValue'
import useFarms from '../../../hooks/useFarms'
import useTokenBalance from '../../../hooks/useTokenBalance'
import useSushi from '../../../hooks/useSushi'
import { getSushiAddress, getLumosPerBlock, getSushiSupply, getMasterChefContract, getLMSPriceMultiplier } from '../../../sushi/utils'
import { getBalanceNumber, getFullDisplayBalance } from '../../../utils/formatBalance'
import Web3 from 'web3'

const PendingRewards: React.FC = () => {
  const [start, setStart] = useState(0)
  const [end, setEnd] = useState(0)
  const [scale, setScale] = useState(1)

  const allEarnings = useAllEarnings()
  let sumEarning = 0
  for (let earning of allEarnings) {
    sumEarning += new BigNumber(earning)
      .div(new BigNumber(10).pow(18))
      .toNumber()
  }

  const [farms] = useFarms()
  const allStakedValue = useAllStakedValue()

  if (allStakedValue && allStakedValue.length) {
    const sumWeth = farms.reduce(
      (c, { id }, i) => c + (allStakedValue[i].totalWethValue.toNumber() || 0),
      0,
    )
  }

  useEffect(() => {
    setStart(end)
    setEnd(sumEarning)
  }, [sumEarning])

  return (
    <span
      style={{
        transform: `scale(${scale})`,
        transformOrigin: 'right bottom',
        transition: 'transform 0.5s',
        display: 'inline-block',
      }}
    >
      <CountUp
        start={start}
        end={end}
        decimals={end < 0 ? 4 : end > 1e5 ? 0 : 3}
        duration={1}
        onStart={() => {
          setScale(1.25)
          setTimeout(() => setScale(1), 600)
        }}
        separator=","
      />
    </span>
  )
}

const Balances: React.FC = () => {
  const [totalSupply, setTotalSupply] = useState<BigNumber>()
  const [lmsPerBlock, setlmsPerBlock] = useState<BigNumber>()
  // const [lmsPriceMultiplier, setlmsPriceMultiplier] = useState<BigNumber>()

  const sushi = useSushi()
  const sushiBalance = useTokenBalance(getSushiAddress(sushi))
  const { account, ethereum }: { account: any; ethereum: any } = useWallet()

  useEffect(() => {
    async function fetchTotalSupply() {
      const supply = await getSushiSupply(sushi)
      setTotalSupply(supply)
    }
    
    async function fetchPerBlock() {
        var _masterContract = getMasterChefContract(sushi);
        const _lmsPerBlock = await getLumosPerBlock(_masterContract)
        const _lmsPriceMultiplier = await getLMSPriceMultiplier(_masterContract)
        // setlmsPriceMultiplier(new BigNumber(Web3.utils.fromWei(_lmsPriceMultiplier.toString())));
        setlmsPerBlock(new BigNumber(Web3.utils.fromWei(_lmsPerBlock.toString())).times(new BigNumber(Web3.utils.fromWei(_lmsPriceMultiplier.toString()))))
    }
    if (sushi) {
      fetchTotalSupply();
      fetchPerBlock();
    }
  }, [sushi, setTotalSupply])

  return (
    <StyledWrapper>
      <CardHome>
        <CardContent>
          <StyledBalances>
            <StyledBalance>
              <SushiIcon />
              <Spacer  />
              <div style={{ flex: 1,marginTop:53 }}>
                <Label text="Your Lumos Balance" />
                <Value
                  value={!!account ? getBalanceNumber(sushiBalance) : 'Locked'}
                />
              </div>
            </StyledBalance>
          </StyledBalances>
        </CardContent>
        <Footnote>
          Pending claim
          <FootnoteValue>
            <PendingRewards /> LMS
          </FootnoteValue>
        </Footnote>
        <Spacer size="lg"/>
      </CardHome>
      <Spacer />

      <CardHome>
        <CardContent>
          <div style={{ flex: 1,marginTop:53 }}></div>
          <Label text="Total Lumos Supply" />
          <Value
            value={totalSupply ? getBalanceNumber(totalSupply) : 'Locked'}
          />
        </CardContent>
        <Footnote>
          New rewards per block
          <FootnoteValue>{lmsPerBlock ? lmsPerBlock.toFixed(4,1) :'Locked'}</FootnoteValue>
        </Footnote>
        <Spacer size="lg"/>
      </CardHome>

      {/*      <Spacer /> 
      <CardHome>
        <CardContent>
          <div style={{ flex: 1,marginTop:53 }}></div>
          <Label text="Lumos Price Multiplier" />
          <Value
            value={lmsPriceMultiplier ? lmsPriceMultiplier.toFixed(2) : 'Locked'}
          />
        </CardContent>
      </CardHome>
      <Spacer />
      <CardHome>
        <CardContent>
          <div style={{ flex: 1,marginTop:53 }}></div>
          <Label text="Lumos Price Multiplier" />
          <Value
            value={lmsPriceMultiplier ? lmsPriceMultiplier.toFixed(2) : 'Locked'}
          />
        </CardContent>
      </CardHome>     */}
    </StyledWrapper>
  )
}

const Footnote = styled.div`
    width: 300px;
    justify-content: space-between;
    display: flex;
`
const FootnoteValue = styled.div`
  font-family: 'bodoni';
  float: right;
`

const StyledWrapper = styled.div`
  max-width: 1050px;
  flex-wrap: wrap;
  justify-content: center;
  align-items: center;
  display: flex;
`

const StyledBalances = styled.div`
  display: flex;
`

const StyledBalance = styled.div`
  align-items: center;
  display: flex;
  flex: 1;
`

export default Balances
